import React from 'react';
import { DataGrid } from '@material-ui/data-grid'
import { rowSelected } from '../../actions/row';
import { useDispatch } from 'react-redux';


export function ShowDataOnTable({
  style = {height: 250}, 
  rows = [{id: 1, col1: 'Hello', col2: 'World' }], 
  columns = [{ field: 'col1', headerName: 'Column 1', width: 150 }],
}) {

  const dispatch = useDispatch()

  return (
    <div style={ style }>
      <DataGrid 
        rows={rows} 
        columns={columns} 
        pageSize={ 10 }
        autoPageSize
        getRowId = { row => row._id || row.id}
        //onCellClick ={e => dispatch( rowSelected(e.row) ) }
        onRowClick = {e => dispatch( rowSelected(e.row) ) }
      />
    </div>
  );
}