import React from 'react'
import { Link } from 'react-router-dom'



export const OptionsMenus = () => {
  const items = [
    {
      id: '1',
      title: 'Modulo Employees',
      grid:'a',
      disabled: false,
      route: 'employees'

    },

    {
      id: '2',
      title: 'Modulo Schools',
      grid: 'b',
      disabled: false,
      route: 'schools'
    },

    {
      id: '3',
      title: 'Modulo Absences',
      grid: 'c',
      disabled: true,
      route: ''
    },
    {
      id: '4',
      title: 'Modulo Contracts',
      grid: 'd',
      disabled: true,
      route: ''
    },

    {
      id: '5',
      title: 'Modulo Enroll',
      grid: 'e',
      disabled: true,
      route: ''
    }
  ]


  return (
    items.map(item => (
      <div 
        className={ 
          (!item.disabled) 
          ? 'dash__modules-option_menu dash__modules-option_enabled' 
          : 'dash__modules-option_menu dash__modules-option_disabled'
        } 
        key={item.id}
        style={{gridArea: item.grid}}
      >
        {
          (!item.disabled) 
          ? <Link 
              to={`/${item.route}`}
              style={{textDecoration: 'none'}}
            >
            <div className='dash__module-link_enabled'>
              { item.title }
            </div>
            </Link> 
          : <p
              className='dash__module-link_disabled'
            >
            { item.title }
          </p>
        }
        
      </div>

    ))

    
  )
}
