import { types } from '../types/types'

const initialState = {
  isAuth: false
}

export const authReducer = (state = initialState, action) => {
  switch (action.type) {

    case types.LoggedIn:
      return {
        ...state,
        ...action.payload,
        isAuth: true
      }

      case types.Loggedout:
        return {
          isAuth: false
        }
      
      case types.AuthChecking:
        return {
          ...state,
          isAuth: true
        }

    default:
      return state
  }
}